@import '../../../../index.scss';

.project {
    width: 30%;
    line-height: 0.8em;
    text-decoration: none;
    color: $main_text_color;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
    border-radius: 16px;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    background: rgba(19, 47, 76, 0.4);
    backdrop-filter: blur(10px);
    border: 2px solid transparent;
    background-clip: padding-box;

    &:hover {
        transform: translateY(-10px) scale(1.02);
        box-shadow: 0 20px 40px rgba(0, 212, 255, 0.2), 0 0 40px rgba(179, 102, 255, 0.15);
        
        .projectImage {
            transform: scale(1.1);
        }
    }

    // Category-based border colors
    &.socialNetwork {
        border-color: $teal_accent;
        box-shadow: inset 0 0 20px rgba(6, 219, 168, 0.1);
    }

    &.todoList {
        border-color: $purple_primary;
        box-shadow: inset 0 0 20px rgba(179, 102, 255, 0.1);
    }

    &.internetStore {
        border-color: $cyan_primary;
        box-shadow: inset 0 0 20px rgba(0, 212, 255, 0.1);
    }

    .projectImage {
        width: 100%;
        height: 220px;
        object-fit: cover;
        transition: transform 0.3s ease;
    }

    .projectContent {
        background: rgba(19, 47, 76, 0.6);
        padding: 24px;
        flex: 1;
        display: flex;
        flex-direction: column;

        .title {
            margin-block-start: 0.5em;
            margin-block-end: 0.5em;
            font-size: 24px;
            font-weight: 600;
            color: #fff;
            letter-spacing: -0.5px;
        }

        .text {
            line-height: normal;
            color: $text_secondary;
            font-size: 14px;
            margin-block-end: 1em;
            flex: 1;
        }

        .technologies {
            display: flex;
            flex-wrap: wrap;
            gap: 8px;
            margin-top: auto;
            margin-bottom: 16px;

            .tech {
                display: inline-block;
                padding: 6px 12px;
                background: rgba(0, 212, 255, 0.1);
                border: 1px solid rgba(0, 212, 255, 0.3);
                border-radius: 20px;
                font-size: 12px;
                color: $cyan_primary;
                font-weight: 500;
                transition: all 0.2s ease;

                &:hover {
                    background: rgba(0, 212, 255, 0.2);
                    border-color: $cyan_primary;
                }
            }
        }

        .viewBtn {
            display: inline-block;
            padding: 10px 24px;
            background: linear-gradient(135deg, $cyan_primary 0%, $teal_accent 100%);
            color: #000;
            text-decoration: none;
            border-radius: 8px;
            font-weight: 600;
            font-size: 14px;
            transition: all 0.3s ease;
            border: none;
            cursor: pointer;
            text-align: center;

            &:hover {
                box-shadow: 0 8px 16px rgba(0, 212, 255, 0.4);
                transform: scale(1.05);
            }
        }
    }
}

@media screen and (max-device-width: 1100px) {
    .project {
        width: 80%;
        cursor: pointer;
        margin-bottom: 50px;

        .text {
            line-height: normal;
        }
    }
}

@media screen and (max-device-width: 576px) {
    .project {
        width: 100%;
        
        .title {
            line-height: normal;
        }
    }
}